import React, { PureComponent, Component, memo } from 'react';

import SubjectRow from './SubjectRow';
import './style.scss';

const day_titles = {
    1: "",
    2: "",
    3: "",
    4: "",
    5: "",
    6: "",
    7: ""
};




export default class Day extends PureComponent {
    
    render() {
        let rows = [];
        let subj;

        for (subj in this.props.day_data) {
            rows.push(<SubjectRow key={subj} path={this.props.day_num + '.' + subj} manifest={this.props.manifest} lesson_data={this.props.day_data[subj]}/>);
        }

        return (
            
            <div className="UIBlock">
                <h1>{ day_titles[this.props.day_num] }</h1>
                <div className="content">
                    { rows }
                </div>
            </div>
        );
    }

}
